*{
    font-family: Arial, Helvetica, sans-serif;
}

body{
    background-color: lightgrey;
    padding: 60px;
}

input{
    color: grey;
    font-size: 1.3rem;
    width: 100%;
}

.img{
    text-align: center;
}

.title{
    text-align: center;
}

.loading{
    font-size: 50px;
}

.card-grid{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    padding: 5px;
    text-align: center;
}

.card img{
    max-height: 170px;
}